@import url(https://fonts.googleapis.com/css?family=Kalam:300,400,700&display=swap);
.header {
  padding: 0.75rem 10.625rem;
  display: flex;
  justify-content: space-between;
  background: #FFFFFF;
  box-shadow: 0px 3px 11px rgba(0, 0, 0, 0.08); }
  .header .header-name {
    font-size: 1.25rem;
    line-height: 2rem;
    letter-spacing: 0.0195rem; }
    .header .header-name .header-name-first {
      color: #3340FF; }
  .header nav {
    display: inline; }
    .header nav a {
      margin-left: 6.5rem;
      font-size: 1.25rem;
      line-height: 2rem;
      text-align: justify;
      letter-spacing: 0.0195rem;
      text-decoration: none;
      color: #787878; }
    .header nav .active {
      color: #3340FF; }

.home {
  background: url(/static/media/background.30ae2973.png);
  background-size: cover; }

.home-landing {
  display: grid;
  grid-template-columns: 49% auto;
  grid-column-gap: 20%;
  padding: 5.3125rem  10.625rem; }
  .home-landing .home-landing-title {
    font-weight: bold;
    font-size: 4rem;
    line-height: 4.625rem;
    letter-spacing: 0.1407rem;
    color: #000000; }
  .home-landing .home-landing-desc {
    padding-top: 2.375rem;
    font-style: normal;
    font-weight: 300;
    font-size: 1.875rem;
    line-height: 2.875rem;
    letter-spacing: 0.0313rem;
    color: #787878; }
  .home-landing .home-landing-work {
    border: 0.125rem solid #3340FF;
    border-radius: 0.4375rem;
    display: inline-block;
    font-size: 1.25rem;
    line-height: 2.875rem;
    letter-spacing: 0.0125rem;
    padding: 0.0625rem 2.5rem;
    color: #3340FF;
    margin-top: 3.5rem; }
  .home-landing img {
    width: 80%; }

body {
  font-family: 'Kalam', cursive; }

html {
  font-size: 1.1vw; }

